import React, { useRef, useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { ProjectCard } from '../components/ProjectCard';
import { ProjectGrid } from '../components/ProjectGrid';
import styled from '@emotion/styled';
import { analyzePortfolioByFile } from '../services/ai';
import { saveProject, getProjects, ProjectData } from '../utils/storage';

const PageContainer = styled.div`
  min-height: 100vh;
  background-color: #131416;
  color: #FFFFFF;
  padding: 2rem;
  padding-top: 7rem;
`;

const UploadSection = styled.div`
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 24px;
  padding: 3rem;
  margin-bottom: 4rem;
  text-align: center;
  margin-top: 7rem;
`;

const UploadTitle = styled.h2`
  font-family: 'Press Start 2P', monospace;
  font-size: 32px;
  margin-bottom: 1.5rem;
`;

const UploadDescription = styled.p`
  font-family: 'Inter', sans-serif;
  font-size: 18px;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 2rem;
`;

const UploadButton = styled.button`
  background: #FFFFFF;
  color: #000000;
  border: none;
  border-radius: 16px;
  padding: 16px 40px;
  font-family: 'Inter', sans-serif;
  font-size: 20px;
  font-weight: 500;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  gap: 8px;
`;

const HiddenInput = styled.input`
  display: none;
`;

const PortfolioReview = () => {
  const navigate = useNavigate();
  const fileInputRef = useRef<HTMLInputElement>(null);
  const [projects, setProjects] = useState<ProjectData[]>([]);

  // 
  useEffect(() => {
    const loadProjects = () => {
      const loadedProjects = getProjects();
      console.log(':', loadedProjects); // 
      setProjects(loadedProjects);
    };
    
    loadProjects();
    
    // 
    const handleFocus = () => {
      loadProjects();
    };
    
    window.addEventListener('focus', handleFocus);
    return () => window.removeEventListener('focus', handleFocus);
  }, []);

  const handleUploadClick = () => {
    fileInputRef.current?.click();
  };

  const handleFileChange = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file && file.type === 'application/pdf') {
      try {
        // ID
        const projectId = Date.now().toString();
        const projectName = file.name.replace('.pdf', '');
        
        // base64
        const reader = new FileReader();
        reader.onload = async (e) => {
          const pdfBase64 = e.target?.result as string;
          
          // PDF"AI..."
          const tempProject = {
            id: projectId,
            name: projectName,
            fileName: file.name,
            uploadTime: new Date().toISOString(),
            pdfBase64: pdfBase64,
            aiSuggestion: 'AI ...',
            fileObj: file
          };
          
          navigate('/portfolio-detail', {
            state: {
              project: tempProject,
              file: pdfBase64
            }
          });
          
          // 
          try {
            const result = await analyzePortfolioByFile(file);
            const finalProject: ProjectData = {
              ...tempProject,
              aiSuggestion: result.suggestion || 'AI'
            };
            
            // 
            saveProject(finalProject);
            
            // 
            setProjects(getProjects());
          } catch (error) {
            console.error('AI:', error);
            const errorProject: ProjectData = {
              ...tempProject,
              aiSuggestion: 'AI'
            };
            saveProject(errorProject);
            setProjects(getProjects());
          }
        };
        reader.readAsDataURL(file);
      } catch (error) {
        alert('');
      }
    } else {
      alert(' PDF ');
    }
  };

  const handleProjectClick = (project: ProjectData) => {
    console.log(':', project); // 
    if (project.pdfBase64) {
      navigate('/portfolio-detail', { 
        state: { 
          project: project,
          file: project.pdfBase64
        } 
      });
    }
  };

  // 
  const getDisplayProjects = () => {
    const displayProjects = [...projects];
    
    // 3
    while (displayProjects.length < 3) {
      displayProjects.push({
        id: `empty-${displayProjects.length}`,
        name: `Project ${displayProjects.length + 1}`,
        fileName: '',
        uploadTime: '',
        pdfBase64: '',
        aiSuggestion: '',
      });
    }
    
    console.log(':', displayProjects.slice(0, 3)); // 
    return displayProjects.slice(0, 3);
  };

  const formatDate = (dateString: string) => {
    if (!dateString) return 'No upload yet';
    
    const date = new Date(dateString);
    const now = new Date();
    const diffInHours = Math.floor((now.getTime() - date.getTime()) / (1000 * 60 * 60));
    
    if (diffInHours < 1) return 'Just now';
    if (diffInHours < 24) return `${diffInHours} hours ago`;
    
    const diffInDays = Math.floor(diffInHours / 24);
    return `${diffInDays} days ago`;
  };

  return (
    <PageContainer>
      <UploadSection>
        <UploadTitle>Upload Your Portfolio</UploadTitle>
        <UploadDescription>
          Share your design work and get personalized AI feedback to improve your portfolio
        </UploadDescription>
        <UploadButton onClick={handleUploadClick}>
          Upload Portfolio
        </UploadButton>
        <HiddenInput
          type="file"
          ref={fileInputRef}
          accept=".pdf"
          onChange={handleFileChange}
        />
      </UploadSection>
      <ProjectGrid>
        {getDisplayProjects().map((project, index) => (
          <ProjectCard
            key={project.id}
            image={project.pdfBase64 ? "/images/pdf-icon.png" : `/images/project${index + 1}.jpg`}
            title={project.name}
            date={formatDate(project.uploadTime)}
            onClick={() => handleProjectClick(project)}
          />
        ))}
      </ProjectGrid>
    </PageContainer>
  );
};

export default PortfolioReview; 