import React, { useState, useEffect } from 'react';
import styled from '@emotion/styled';
import { useLocation } from 'react-router-dom';
import { analyzeAudioByFile } from '../services/ai';

const Container = styled.div`
  min-height: 100vh;
  background: linear-gradient(120deg, #181a20 60%, #2b2250 100%);
  display: flex;
  align-items: flex-start;
  padding: 100px 2rem 2rem 2rem;
`;

const Left = styled.div`
  flex: 2;
  background: #222;
  border-radius: 24px;
  margin-right: 2rem;
  min-height: 600px;
  max-width: 700px;
  max-height: 900px;
  overflow: auto;
  padding: 2rem;
  color: #fff;
  font-size: 18px;
`;

const Right = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 2rem;
`;

const SuggestionBox = styled.div`
  background: rgba(255,255,255,0.08);
  border-radius: 16px;
  padding: 2rem;
  min-height: 200px;
  color: #fff;
  font-size: 18px;
`;

const AudioDetail: React.FC = () => {
  const location = useLocation() as any;
  const audioObj = location.state?.audio;
  const fileObj = audioObj?.file;

  const [asrText, setAsrText] = useState<string>('AI ...');
  const [aiSuggestion, setAiSuggestion] = useState<string>('AI ...');

  useEffect(() => {
    if (fileObj) {
      setAsrText('AI ...');
      setAiSuggestion('AI ...');
      analyzeAudioByFile(fileObj).then(res => {
        setAsrText(res.asr_text || '');
        setAiSuggestion(res.suggestion || 'AI');
      }).catch(() => {
        setAsrText('');
        setAiSuggestion('AI');
      });
    }
  }, [fileObj]);

  return (
    <Container>
      <Left>
        <div style={{ fontWeight: 700, fontSize: 20, marginBottom: 12 }}></div>
        <div>{asrText}</div>
      </Left>
      <Right>
        <SuggestionBox>
          <div style={{ fontWeight: 700, fontSize: 20, marginBottom: 12 }}>AI </div>
          <div>{aiSuggestion}</div>
        </SuggestionBox>
      </Right>
    </Container>
  );
};

export default AudioDetail; 